  5%  { border-radius: 50% 52% 41% 59% / 66% 37% 63% 34%; }
  10% { border-radius: 41% 31% 71% 29% / 67% 31% 69% 33%; }
  15% { border-radius: 45% 40% 71% 29% / 63% 65% 35% 37%; }
  20% { border-radius: 52% 39% 51% 49% / 57% 58% 42% 43%; }
  25% { border-radius: 55% 34% 25% 75% / 47% 30% 70% 53%; }
  30% { border-radius: 60% 68% 38% 62% / 65% 60% 40% 35%; }
  35% { border-radius: 63% 37% 41% 59% / 35% 38% 62% 65%; }
  40% { border-radius: 61% 43% 49% 51% / 55% 71% 29% 45%; }
  45% { border-radius: 57% 53% 34% 66% / 65% 36% 64% 35%; }
  50% { border-radius: 63% 56% 32% 68% / 69% 26% 74% 31%; }
  55% { border-radius: 67% 72% 37% 63% / 71% 44% 56% 29%; }    
  60% { border-radius: 71% 62% 35% 65% / 74% 53% 47% 26%; }
  65% { border-radius: 73% 27% 46% 54% / 54% 47% 53% 46%; }
  70% { border-radius: 75% 25% 47% 53% / 49% 53% 47% 51%; }
  75% { border-radius: 78% 38% 43% 57% / 55% 60% 40% 45%; }
  80% { border-radius: 74% 59% 65% 35% / 73% 50% 50% 27%; }
  85% { border-radius: 68% 45% 57% 43% / 73% 61% 39% 27%; }
  90% { border-radius: 64% 26% 33% 67% / 40% 65% 35% 60%; }
  95% { border-radius: 59% 42% 57% 43% / 53% 45% 55% 47%; }
  100%{ border-radius: 52% 52% 68% 32% / 42% 28% 72% 58%; }
}
